import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { Alert } from "./Alert";
import { List } from "./List";
import { useState } from "react";
import { AiOutlineClose } from "react-icons/ai";

import "./App.css";

function App() {
  const [text, setText] = useState("");
  const [todoItem, setTodoItem] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
  const [editId, setEditId] = useState(null);
  const [alertMsg, setAlertMsg] = useState({ show: false, msg: "", type: "" });

  const submitTodo = (e) => {
    e.preventDefault();

    if (text === "") {
      alertFields(true, "Please enter something", "danger");
    } 
    
    else if (text && isEditing) {
     setTodoItem(
       todoItem.map(item => {
          if(item.id === editId){
            return {...item, value:text}
          }
          return item;
       })
     )
     setText('');
     setEditId(null);
     setIsEditing(false);
     alertFields(true, "Item edited successfully", "success");

    } 
    
    else {
      const listItem = {
        id: Date.now(),
        value: text,
      };
      setTodoItem([...todoItem, listItem]);
      alertFields(true, "Item added successfully", "success");
      setText("");
    }
  };
  const alertFields = (show = false, msg = "", type = "") => {
    setAlertMsg({ show, msg, type });
  };

  const deleteItem = (id) => {
    let newArr = todoItem.filter(item => item.id !== id);
    alertFields(true, "Item deleted", "danger");
    setTodoItem(newArr);
  }

  const deleteAll = () => {
    setTodoItem([])
  }
  const editItem = (id) => {
    const specificItem = todoItem.find(item => item.id === id);
    setIsEditing(true);
    setEditId(id);
    setText(specificItem.value);
  }

  return (
    <div className="App">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="todo-app text-center">
              <form onSubmit={submitTodo}>
                {alertMsg.show && <Alert removeAlert={alertFields} {...alertMsg} todoItem={todoItem} />}
                <div className="heading">
                  <h1>Your Todo</h1>
                </div>
                <div className="input-wrapper d-flex align-items-center">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Create your todo list"
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                  />
                  <button type="submit" className="btn btn-primary">
                    {isEditing ? "Edit" : "Submit"}
                  </button>
                </div>
              </form>
              {todoItem.length > 0 && (
                <div className="">
                  <List item={todoItem} deleteItem={deleteItem} editItem={editItem} />
                  <button className="btn btn-danger" onClick={deleteAll}>Clear Items <AiOutlineClose/></button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
