import React from "react";
import { AiFillEdit, AiFillDelete } from "react-icons/ai";

export const List = ({item,deleteItem,editItem}) => {
  return (
    <ul className="list-unstyled mt-4">
      {item.map((item) => {
        return (
          <li key={item.id}>
            <p className="mb-0" key={item.id}>
              {item.value}
            </p>
            <div className="controls">
              <span className="edit" onClick={() => editItem(item.id)}>
                <AiFillEdit />
              </span>
              <span className="delete" onClick={() => deleteItem(item.id)}>
                <AiFillDelete />
              </span>
            </div>
          </li>
        );
      })}
    </ul>
  );
};
