import React, { useEffect } from 'react'

export const Alert = ({type,msg,removeAlert,todoItem}) => {

    useEffect(() => {
        const timeout = setTimeout(() => {
            removeAlert();            
        }, 2000);
        return () => {
            clearTimeout(timeout);
        }
    },[todoItem])
    return (
        <div>
            <p className={`alert ${type}`}>{msg}</p>
        </div>
    )
}
